@import url(https://fonts.googleapis.com/css?family=Open+Sans);

@font-face {
    font-family: "Muller";
    src: local("muller"), url(/itmo-test/static/media/muller.3df62da4.ttf) format("opentype");
}

body {
    margin: 0;
    color: #222;
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4 {
    font-family: "Muller";
    font-style: normal;
    font-weight: bold;
}

.click-more {
    background: #f21f5b;
    cursor: pointer;
    display: inline-block;
    padding: 7px 25px;
    margin: 0;
    border-radius: 50px;
    -moz-border-radius: 50px;
    -webkit-border-radius: 50px;
    color: #fff;
    text-decoration: none;
}

a {
    text-decoration: none;
}

.header{width:100%;height:120px;display:flex;flex-direction:column}.header .logo{flex:1 1;background:linear-gradient(90deg, #3951e7 20.77%, #832ab9 91.64%);display:flex;align-items:center}.header .logo img{height:16px;width:auto;margin-left:2%}.header .head{flex:2 1;display:flex;justify-content:space-between;align-items:center;align-content:center;margin:0 25px}.header .head a{color:#222;text-decoration:none}.header .head .head-name{font-family:"Muller";font-size:28px;font-style:normal;font-weight:700;line-height:28px;letter-spacing:0em;text-align:left}.header .head .menu a{margin:0 20px;font-weight:normal}
.banner{color:#fff;width:100%;height:484px;display:flex;align-content:center;justify-content:center;align-items:center}.banner img{z-index:-1}.banner i{font-size:25pt;margin:5%;cursor:pointer;transition:.5s}.banner i:hover{color:#f21f5b}.banner .banner-cards{width:45%;z-index:1}.banner .banner-cards .overflow{overflow:hidden;height:inherit}.banner .banner-cards .overflow .banner-card{display:flex;flex-direction:column;flex-wrap:nowrap;align-items:flex-start;float:left}.banner .banner-cards .overflow .banner-card h1{font-family:"Muller"}.banner .banner-cards .overflow .banner-card p{width:75%;margin:20px 0}.banner .banner-cards .overflow .banner-card .click-more{background:#f21f5b;font-family:"ALS Schlange Sans",sans-serif;font-size:18px;font-weight:400;line-height:22px}
.modal{height:100vh;width:100vw;background-color:rgba(33,63,199,.4);position:fixed;top:0;left:0;display:flex;align-items:center;justify-content:center;opacity:0;pointer-events:none;transition:.5s}.modal.active{opacity:1;pointer-events:all}.content{padding:20px;border-radius:12px;background-color:#fff}
.contacts{position:relative}.yamaps{width:100%;height:400px;background-color:rgba(124,111,111,.5)}.info{position:absolute;top:14%;left:20%;background-color:#fff;width:400px;height:280px;border-radius:8px;box-shadow:0 0 10px rgba(90,88,88,.4);z-index:1;padding:20px 40px}.info a i{margin-right:7px}
h2{margin-top:4%}.cards{margin:0 auto;display:flex;flex-wrap:wrap;align-content:center;justify-content:center;align-items:center}.news_card{display:flex;flex-direction:column;align-content:center;box-shadow:0 0 16px rgba(0,0,0,.16);border-radius:16px;width:300px;background-color:#fff;margin:20px}.news_card .newscontent{margin:0 25px}.news_card .addinfo{display:flex;height:-webkit-min-content;height:min-content;justify-content:space-between}.news_card .addinfo .date{text-transform:uppercase;color:#6a6a6a;mix-blend-mode:normal;font-size:12px;margin-top:revert;margin-bottom:revert}.news_card .small{width:inherit;border-top-left-radius:15px;border-top-right-radius:15px}.news_card .text p{margin-top:-5px;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:5;-webkit-box-orient:vertical}.news_card .text p strong{font-weight:normal}
footer{position:relative;width:100%;height:300px}footer a{font-style:normal;font-weight:400;line-height:18px;text-decoration:none;color:#fff}footer .top-block{height:260px;background-color:#232473;display:flex}footer .top-block ul li{padding:6px 0}footer .botton-block{display:flex;justify-content:space-around;align-items:center;align-content:center;left:0;bottom:-40px;background-color:#1a1b57;height:40px}footer .botton-block a{font-size:13px}
